* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

div,
button,
p,
span,
i {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

html {
  font-size: 10px;
  font-family: 'Roboto Condensed', sans-serif;
}

body {
  background-image: url(/15-puzzle/static/media/bg.e79067ea.jpg);
  background-repeat: repeat;
  color: #F1FAEE;
  width: 100vw;
  height: 100vh;
  min-width: 320px;
  min-height: 320px;
  background-color: #000;
}
.App {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
}

header {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-basis: 15%;
}

main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  flex-grow: 1;
}

aside {
  -webkit-transition: 2s;
  transition: 2s;
}

footer {
  /* padding-bottom: 10px; */
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 5px;
  background-color: rgba(0, 0, 0, .7);
}


.blur {
  -webkit-filter: blur(4px);
          filter: blur(4px)
}
header h1 {
  text-align: center;
  font-family: 'Merienda One', cursive;
  font-size: 5em;
  width: 100vw;
  min-width: 320px;
  padding: 10px;
  color: #f5cb5c;
}

@media(max-width:460px) {
  header h1 {
    font-size: 4em;
  }
}

@media(min-height:1360px) {
  header {
    flex-basis: 20%;
  }

  header h1 {
    align-self: flex-end;
    font-size: 10em;
    flex-grow: 1;
  }

}

@media(max-height:430px) and (orientation: landscape) {
  header h1 {
    font-size: 4em;
  }
}
.homescreen {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  flex-grow: 1;
}

.homescreen h2 {
  font-size: 3em;
  text-transform: uppercase;
  letter-spacing: 2px;
}

.settings {
  display: flex;
  width: 75vw;
  justify-content: space-around;
  align-items: center;
}

.settings div {
  position: relative;
  width: 15vw;
  height: 15vw;
  min-width: 76px;
  min-height: 76px;
  border: 1px solid black;
  border-radius: 20%;
  background-color: #e8eddf;
  color: #4A525A;
  cursor: pointer;

}

.settings div *::-moz-selection {
  background-color: transparent
}

.settings div *::selection {
  background-color: transparent
}

.settings div p {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 3.6em;
  text-transform: uppercase;
  letter-spacing: 3px;
  z-index: 2;
  -webkit-transition: .3s;
  transition: .3s;
}

.settings div span {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: 'Merienda One', cursive;
  font-size: 7em;
  opacity: 0;
  -webkit-transition: .3s;
  transition: .3s;
}

.settings div:hover {
  background-color: #f5cb5c;
}

.settings div:hover p {
  opacity: 0
}

.settings div:hover span {
  opacity: 1
}

.homescreen h3 {
  padding: 10px;
  width: 65%;
  font-size: 1.8em;
  text-align: center;
  text-transform: uppercase;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, .6);
  color: #f5cb5c;
}

@media(max-width: 1126px) {
  .homescreen {
    font-size: 8px;
  }

}

@media(max-width: 905px) {
  .homescreen {
    font-size: 7px;
  }

  .settings div {
    width: 20vw;
    height: 20vw;
  }
}

@media(max-width: 800px) {
  .settings {
    font-size: 5px;
  }
}

@media(max-width: 590px) {
  .settings div {
    width: 20vw;
    height: 20vw;
  }
}

@media(max-width: 430px) {
  .settings {
    font-size: 4px;
  }

  .settings div {
    width: 22vw;
    height: 22vw;

  }
}
.Tile {
  position: relative;
  border: 1px solid #000;
  border-radius: 20%;
  -webkit-transition: .4s;
  transition: .4s;
  cursor: pointer;
}

.Tile p {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-family: 'Merienda One', cursive;
  font-size: 5em;
  ;
  color: #1D3557;
}

.TileZero {
  border-radius: 20%;
}
.board {
  display: flex;
  position: relative;
  flex-wrap: wrap;
  align-content: flex-start;
  width: 576px;
  height: 576px;
  min-width: 208px;
  min-height: 208px;
}


.fa-question-circle {
  position: absolute;
  top: 0;
  right: 0;
  -webkit-transform: translate(75%, -75%);
          transform: translate(75%, -75%);
  font-size: 5em;
  cursor: pointer;
  -webkit-transition: .2s;
  transition: .2s
}

.fa-question-circle:hover {
  color: #f5cb5c;
}


@media(max-width:700px) {
  .board {
    width: 80vw;
    height: 80vw;
  }
}


@media(max-width:500px) {
  .board {
    font-size: 8px
  }
}

@media(max-width:380px) {
  .board {
    font-size: 7px
  }
}

@media(max-height:800px) and (orientation:landscape) {
  .board {
    width: 65vh;
    height: 65vh;
  }
}

@media(max-height:500px) and (orientation:landscape) {
  .board {
    font-size: 8px
  }
}

@media(max-height:360px) and (orientation:landscape) {
  .board {
    font-size: 7px
  }
}
footer {
  padding: 5px;
  font-size: 1.6em;
}

footer p img {
  vertical-align: middle;
}

footer p a {
  color: white;
}

footer p a:hover {
  color: #f5cb5c
}

@media(max-width:800px) {
  footer {
    font-size: 1.4em;
  }
}

@media(max-width:590px) {
  footer {
    font-size: 1em;
  }
}

@media(max-width:420px) {
  footer p {
    text-align: right
  }
}
.won,
.congrat,
.again {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.won {
  flex-basis: 70%;
  justify-content: space-around;
}

.congrat {
  justify-content: center;
  flex-basis: 50%
}

.congrat h2 {
  font-size: 4.8em;
  letter-spacing: 3px;
  font-family: 'Merienda One', cursive;
  margin-bottom: 15px;
}

.congrat p {
  font-size: 26px;
}

.congrat p span {
  color: #f5cb5c;

}

.again {
  flex-grow: 1;
  justify-content: flex-start;
}

.again p {
  font-size: 2em;
  text-align: center;
}

.again button {
  padding: 20px 30px;
  margin-top: 15px;
  font-size: 1.8em;
  letter-spacing: 3px;
  text-transform: uppercase;
  border: 1px solid black;
  border-radius: 15px;
  background-color: #e8eddf;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
}

.again button:hover {
  background-color: #f5cb5c
}

@media (max-width:460px) {
  .congrat h2 {
    font-size: 3.4em;
  }

  .congrat p,
  .again p {
    font-size: 2.6em;
  }

}
.faqContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 800px;
  height: 410px;
  min-height: 250px;
  min-width: 250px;
  padding: 10px;
  border-radius: 20px;
  background-color: rgba(0, 0, 0, .8);
  text-align: center;
  z-index: 3;
}

.fa-times-circle {
  position: absolute;
  top: 0;
  right: 0;
  -webkit-transform: translate(50%, -50%);
          transform: translate(50%, -50%);
  font-size: 5rem;
  color: #e8eddf;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
}

.fa-times-circle:hover {
  color: #f5cb5c;
}

.tips {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  flex-basis: 35%
}

.tips p {
  font-size: 1.6em;
  letter-spacing: 1px;
}

.cheats {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  flex-grow: 1;
  width: 100%;
}

.cheats h2 {
  font-family: 'Merienda One', cursive;
  font-size: 2.4em;
}

.cheats button {
  width: 268px;
  height: 90px;
  min-width: 192px;
  min-height: 64px;
  background-color: #e8eddf;
  border: 1px solid black;
  border-radius: 15px;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 1.8em;
  letter-spacing: 2px;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: .3s;
  transition: .3s;
  outline: 0;
}

.cheats button:hover {
  background-color: #f5cb5c;
}

.blurActive {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  z-index: 2;
}

@media(max-width:1023px) {
  .faqContainer {
    width: 85vw;
    height: 85vh;
    font-size: 12px;
  }

  .fa-times-circle {
    font-size: 3.6em;
  }
}

@media(max-width:420px) {
  .cheats button {
    width: 65%;
    height: 15%;
  }
}

@media(max-height:420px) {
  .cheats button {
    width: 65%;
    height: 15%;
  }
}
